export default matrix => {
	const {
		interest,
		logic,
		luck,
		health,
		labour,
		personality,
		energy,
		duty,
		memory
   } = matrix;
	/**
	 *       //   .      .
	 */

	let temperament = interest + logic + luck;

	/**
	 * :     //.     .
	 */

	let everyday = health + logic + labour;

	/**
	 * :  ,     / / .
	 */

	let purpose = personality + health + luck;

	/**
	 * :  ,    // .
	 */

	let family = energy + logic + duty;

	/**
	 *  :     //.
	 */

	let habits = interest + labour + memory;

	return {
		temperament,
		everyday,
		purpose,
		family,
		habits
	};
};
